.app-header {
    background-color: #222;
    text-align: center;
    color: white;
    text-transform: uppercase;
    padding: 10px 0;
    border-radius: 2px 2px 0 0;
}
@media screen and (max-width: 510px) { 
.app-header {
    
    border-radius: 0;
}
}
.app-header h1 {
    color: #764abc;
}

.app-header h2 {
    font-size: 1.2rem;
}

button.icon {
    border-radius: 50%;
    font-size: 24px;
    height: 32px;
    margin-left: 0;
    margin-right: 0;
    min-width: 32px;
    width: 32px;
    padding: 0;
    overflow: hidden;
    line-height: normal;
    outline: none;
}

.checkbox {
    position: absolute;
    top: 14px;
    left: 4px;
}
.delete-icon {
    position: absolute;
    top: 14px;
    right: 10px;
}
.important-icon {
    position: absolute;
    top: 14px;
    right: 50px;
}

button .material-icons {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-12px, -12px);
            transform: translate(-12px, -12px);
    line-height: 24px;
    width: 24px;
    vertical-align: middle;
}
.todo.done * {
    color: lightgray;
    transition: color 0.2s;
}

.todo.done span {
    text-decoration: line-through;
}

.todo.important {
    font-weight: bold;
    color: #764abc;
}
.todo span {
    max-width: 75%;
    margin-left: 5%;
    margin-right: auto;
    word-wrap: break-word;
}

.todo-list .todo {
    display: flex;
    font-size: 1rem;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #fff;

    padding: 1em;
    align-items: center;
    list-style: none;
}
.todo-list {
    margin: 0;
}
.todo {
    position: relative;
}
.todo-list {
    padding: 0;
}

.todo .checkbox {
    margin-right: 0.5rem;
}

.todo button:not(.checkbox) {
    opacity: 0;
    transition: all 0.2s;
}

.todo:hover button:not(.checkbox) {
    opacity: 1;
}

.search-panel {
    padding: 10px;
    background-color: #ffffff;
    display: flex;
    flex-wrap: wrap;
}

.search-panel .search-input {
    width: auto;
    flex-grow: 1;
    margin-right: 3px;
}

@media screen and (max-width: 510px) {

  .btn-group {
    margin-top: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
.btn {
  color: #764abc;
}
.btn-active {
  background-color: #764abc;
  color: white;
}
.btn:hover {
  border: 1px solid #764abc;
  background-color: rgba(183, 79, 188, 1);
}

.btn:focus {
  box-shadow: none;
}

.add-form {
    display: flex;
    font-size: 1rem;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #fff;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;

    align-items: stretch;
    padding: 10px;
}

.add-form input {
    width: auto;
    flex: 1 1;
    outline: none;
    transition: all 0.5s;
}

.add-todo-btn {
    outline: none;
}

.add-todo-btn:disabled {
    color: rgba(0, 0, 0, 0.26);
    pointer-events: none;
}

.add-form input:focus {
    border: 1px solid #764abc;
}

body {
  background-color: #fafafa;
}

.todo-app {
  margin: 2rem auto 0 auto;
  max-width: 500px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

input {
  font-size: 1rem;
  color: #757575;
  padding: 0.5em;
  border-radius: 2px;
  border: 1px solid lightgray;
  outline: none;
  transition: all 0.5s;
}
input:focus {
  border: 1px solid #764abc;
}

button {
  background: 0 0;
  border: none;
  border-radius: 2px;
  color: #757575;
  position: relative;

  margin: 0;
  min-width: 64px;
  padding: 0 16px;
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0;
  overflow: hidden;
  will-change: box-shadow;
  transition: all 0.3s;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  line-height: 36px;
  vertical-align: middle;
}
button:hover {
  background-color: rgba(158, 158, 158, 0.2);
}
button:active {
  background-color: rgba(158, 158, 158, 0.4);
}
button {
  margin-left: 10px;
}
button:focus {
  outline: none;
}
@media screen and (max-width: 510px) {
  .todo-app {
    margin: 0;
  }
}

